export default {
    data() {
        return {
			list: [] ,// 用户收货地址列表
			type: ''
        }
    },
	onLoad (e) {
		if(e.type){
			this.type = e.type;
		}
	},
	onShow () {
		this.userShipList();
	},
    methods: {
		// 获取收货地址列表
		userShipList () {
			this.$api.userShip({}, res => {
                if (res.status) {
                    this.list = res.data
                }
            })
		},
		// 收货地址删除
		delShip (id) {
			this.$common.modelShow('提示', '确认删除此收货地址?', () => {
				let data = {
					id: id
				}
				this.$api.removeShip(data, res => {
					if (res.status) {
						this.$common.successToShow(res.msg, () => {
							this.userShipList();
						});
					} else {
						this.$common.errorToShow(res.msg);
					}
				})
			})
		},
		//编辑
		toEdit (id) {
			this.$common.navigateTo('./index?ship_id=' + id);
		},
		//添加
		toAdd() {
			this.$common.navigateTo('./index');
		},
		//选择
		isSelect(data) {
			if(this.type == 'order'){
				let pages = getCurrentPages();//当前页
				let beforePage = pages[pages.length - 2];//上个页面
				



				






				beforePage.$vm.userShip = data;
				beforePage.$vm.params.area_id = data.area_id;

				
				uni.navigateBack({
					delta: 1
				});
			}
		},

		wechatAddress: function () {
			wx.chooseAddress({
				success: res => {
					if (res.errMsg == "chooseAddress:ok") {
						//获取成功
						//存储这个收获地区信息到数据库
						let data = {
							province_name: res.provinceName,
							city_name: res.cityName,
							county_name: res.countyName,
							postal_code: res.postalCode
						};
						let areaId = 0;
						this.$api.getAreaId(data, res1 => {
							if (res1.status) {
								//存储用户收货信息
								let userShipId = 0;
								let userShipData = {
									area_id: res1.data,
									user_name: res.userName,
									detail_info: res.detailInfo,
									tel_number: res.telNumber,
									is_def: 2
								}
								this.$api.saveUserShipWx(userShipData, res2 => {
									if (res2.status) {
										this.$common.errorToShow('存储微信地址成功', r => {
											setTimeout(rp => {
												this.userShipList();
											}, 1000);
										});
									}else{
										uni.showModal({
											title: '提示',
											content: '存储微信地址失败',
											showCancel: false
										});
									}
								});
							}else{
								uni.showModal({
									title: '提示',
									content: '获取微信地址失败',
									showCancel: false
								});
							}
						});
					} else {
						uni.showModal({
							title: '提示',
							content: '获取微信地址失败',
							showCancel: false
						});
					}
				}
			});
		},

    }
}
